function DetilPengajuan(props){
  const [modal, setModal] = useState(false);
  var juki = <NumberFormat value={props.uuid.jumlah} displayType={'text'} thousandSeparator={true} prefix={'Rp'} />
  return(
    <>
    <CModal
    show={modal}
    onClose={()=> setModal(false)}
    size="xl"
    >
    <CModalHeader closeButton><h5 class="Lato">Detil Pengajuan</h5></CModalHeader>
    <CModalBody>
    <CRow>
      <CCol sm="6">
      <CFormGroup>
        <CLabel htmlFor="noaju">No Pengajuan</CLabel>
        <CInput value={props.uuid.uuid} disabled/>
      </CFormGroup>
      <CFormGroup>
        <CLabel htmlFor="subdit">Subdit</CLabel>
        <CTextarea id="subdit" value={props.subdit} disabled/>
      </CFormGroup>
      <CFormGroup>
        <CLabel htmlFor="subdit">Komponen</CLabel>
        <CTextarea id="subdit" value={props.komponen} disabled/>
      </CFormGroup>
      <CFormGroup>
        <CLabel htmlFor="subdit">Subkomponen</CLabel>
        <CTextarea id="subdit" value={props.subkomponen} disabled/>
      </CFormGroup>        
      <CFormGroup>
        <CLabel htmlFor="street">Belanja</CLabel>
        <CInput id="street" value={props.anggaran} disabled />
      </CFormGroup>
      </CCol>
      <CCol sm="6">
      <CFormGroup>
        <CLabel htmlFor="noaju">Judul</CLabel>
        <CInput value={props.judul} disabled/>
      </CFormGroup>
      <CFormGroup>
        <CLabel htmlFor="subdit">Subjudul</CLabel>
        <CTextarea id="subdit" value={props.subjudul} disabled/>
      </CFormGroup>
      <CFormGroup>
        <CLabel htmlFor="subdit">Detil</CLabel>
        <CTextarea id="subdit" value={props.detil} disabled/>
      </CFormGroup>
      <CFormGroup>
        <CLabel htmlFor="subdit">Jumlah</CLabel>
        <CTextarea id="subdit" value={props.uuid.jumlah} disabled/>
      </CFormGroup>  
      <CFormGroup>
        <div><CLabel htmlFor="subdit">Status</CLabel></div>
        <div><CBadge shape="pill" color="success">Tersimpan di Database Danaku</CBadge></div>
      </CFormGroup>                          
      </CCol>
    </CRow>      
    </CModalBody>
    </CModal>
    <div className="px-2">
    <CButton onClick={()=>setModal(true)} size="sm" color="warning">Detil</CButton>                          
    </div>
    </>
  )
}

class ListPengajuan extends Component {
  constructor(props) {
    super(props);
    this.state = {
        listAjuAll:[],
        listAjuFiltered:[
          {uuid:"Loading", subdit: "Loading", belanja: "Loading", kode:"Loading", jumlah:"Loading", timestamp:"Loading"}         
        ],
        redirect:false,
        loading:false,
        modal:false
    };
  }
  componentDidMount = async () => {
    try {
        await doc.useServiceAccountAuth({
            client_email: CLIENT_EMAIL,
            private_key: PRIVATE_KEY,
          });
          // loads document properties and worksheets
          await doc.loadInfo();
          const sheet = doc.sheetsById[SHEET_ID];
          const log = await sheet.getRows();  
          const hayoo = log.filter((item) => {
            return item.pengirim == fireAuth.currentUser.displayName
          })
          var i
          this.setState({listAjuFiltered:[]})
          for (i=0;i<hayoo.length;i++){
              var jancik = {uuid:hayoo[i].uuid, kode:hayoo[i].value, jumlah:hayoo[i].jumlah, timestamp:hayoo[i].timestamp}
              var cuki = this.state.listAjuFiltered
              var jack = cuki.concat(jancik)
              this.setState({listAjuFiltered:jack})
          }   
    } catch (e) {
        console.Loading('Iki Loading e ', e);
    }
  }
  handleClick = () => {
    this.setState({redirect:true})
  } 
  render(){
    console.log("jancik", this.state.listAjuFiltered)
    console.log(this.state.listAjuFiltered)
    if (this.state.redirect) {
      return <Redirect push to="/pengajuan/form" />;
    }  
    return(
      <>
      <CCard>
        <CCardHeader color="light" >
          <h5>Pengajuan Anggaran Kegiatan</h5>
        </CCardHeader>
        <CCardBody>        
        <CDataTable
              fields={fieldlistpengajuan}
              items={this.state.listAjuFiltered}
              itemsPerPage= {3}
              pagination
              columnFilter
              striped
              size="xl"
              border
              outlined
              overTableSlot = {
              <div className="d-flex" >
                <div className="ml-auto pb-2">
                  <CButton onClick={this.handleClick.bind(this)} color="info" >Tambah</CButton>
                </div>
              </div>}
              scopedSlots = {{
                'action':
                  (index)=>{
                    const angka = subdit.findIndex(x => x.value == index.kode.substring(0,10))
                    const subditu = subdit[angka]
                    const angkas = komponen.findIndex(x => x.value == index.kode.substring(0,12))
                    const komponenu = komponen[angkas]
                    const angkass = subkomponen.findIndex(x => x.value == index.kode.substring(0,14))
                    const subkomponenu = subkomponen[angkass]                                         
                    const angkasss = anggaran.findIndex(x => x.value == index.kode.substring(0,22))
                    const anggaranu = anggaran[angkasss] 
                    const angkassss = judul.findIndex(x => x.value == index.kode.substring(0,24))
                    const judulu = judul[angkassss] 
                    const angkasssss = subjudul.findIndex(x => x.value == index.kode.substring(0,26))
                    const subjudulu = subjudul[angkasssss]                                          
                    const angkassssss = detil.findIndex(x => x.value == index.kode)
                    const detilu = detil[angkassssss]
                    var subditi,komponeni,subkomponeni,anggarani,juduli,subjuduli,detili     
                    if(subditu && anggaranu && detilu && komponenu && subkomponenu && judulu && subjudulu){
                      subditi = subditu.label
                      komponeni = komponenu.label
                      subkomponeni = subkomponenu.label
                      anggarani = anggaranu.label
                      juduli = judulu.label
                      subjuduli = subjudulu.label
                      detili = detilu.label
                    } else {
                    }                                                    
                  return(
                    <td>
                      <CRow>
                        <DetilPengajuan uuid={index} subdit={subditi} komponen={komponeni} subkomponen={subkomponeni} anggaran={anggarani} judul={juduli} subjudul={subjuduli} detil={detili}/>
                        <div>
                        <CPopover content="Untuk mengedit, harap hubungi seksi TOP">
                          <CButton size="sm" color="info">Edit</CButton>
                        </CPopover>                                                  
                        </div>                       
                      </CRow>                      
                    </td>
                  )
                  },
                  'subdit':
                  (index)=>{
                    const angka = subdit.findIndex(x => x.value == index.kode.substring(0,10))
                    const ini = subdit[angka] 
                    var cik
                    if(ini){
                      cik = ini.label
                    } else {
                      cik = "Loading"
                    }                    
                  return(
                    <td>
                      {cik}
                    </td>
                  )
                  },
                  'belanja':
                  (index)=>{
                    const angka = anggaran.findIndex(x => x.value == index.kode.substring(0,22))
                    const ini = anggaran[angka]  
                    var cik
                    if(ini){
                      cik = ini.label
                    } else {
                      cik = "Loading"
                    }                                       
                  return(
                    <td>
                      {cik}
                    </td>
                  )
                  },                                    
                  'kode':
                  (index)=>{
                    const angka = detil.findIndex(x => x.value == index.kode)
                    const ini = detil[angka]
                    var cik
                    if(ini){
                      cik = ini.label
                    } else {
                      cik = "Loading"
                    }                                        
                  return(
                    <td>
                      {cik}
                    </td>
                  )
                  }, 
                  'jumlah':
                  (index)=>{                   
                  return(
                    <td>
                      <NumberFormat value={index.jumlah} displayType={'text'} thousandSeparator={true} prefix={'Rp'} />
                    </td>
                  )
                  },                                    
                }}
        />
        </CCardBody>
      </CCard>
      </>
      )
  }  
}

export default ListPengajuan
